.todo-item-container {
  color: white;
  text-align: left;
}
.ulist{
  margin: 0;
  padding: 0;
}
li{
  list-style-type: none;
  margin-bottom:15px;
}

.li-expander{
    display:grid;
    grid-template-columns: 1fr 6fr 1fr;
}
.form-container input{
font-size: 26px;;
}
.form-container button {
  margin:30px;
  font-size:26px;
}
.spantext{
  width:100%;
  padding:0;
  margin:0;
}
h1{
  color: linear-gradient(to right, red, pink);
  background-image: linear-gradient(to right, red, pink);
  position:sticky;
  top:0;
  z-index:1000;
}